<template>
  <div class="page-login">
    <div class="l-header">
      <router-link :to="{name:'index'}" class="site-logo"></router-link>
    </div>
    <div class="l-content">
      <div class="l-left">
        <img
          src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg"
          width="480"
          height="370"
          alt="美团网"
        >
      </div>
      <div class="l-form">
        <div class="validate-info" :style="{visibility: flak}">
          <i class="el-icon-remove"></i>
          <span>{{msg}}</span>
        </div>
        <p>账号登录</p>
        <el-input placeholder="请输入用户名" prefix-icon="el-icon-s-custom" v-model="userName"></el-input>
        <el-input
          placeholder="请输入密码"
          type="password"
          prefix-icon="el-icon-unlock"
          v-model="password"
        ></el-input>
        <p class="forget">
          <a href="#">忘记密码？</a>
        </p>

        <el-button type="primary" @click="login">登陆</el-button>
        <p>
          <span>还没有账号？</span>
          <router-link :to="{name:'register'}">免费注册</router-link>
        </p>
        <div class="other">
          <p>用合作网站账号登录</p>
          <a href="#" class="qq"></a>
          <a href="#" class="weibo"></a>
        </div>
      </div>
    </div>
    <div class="l-footer">
      <ul class="clearfix">
        <li class="f-list"><a href="#">关于美团</a></li>
        <li class="f-list"><a href="#">加入我们</a></li>
        <li class="f-list"><a href="#">商家入驻</a></li>
        <li class="f-list"><a href="#">帮助中心</a></li>
        <li class="f-list"><a href="#">美团手机版</a></li>
      </ul>
    </div>
  </div>
</template>

<script>
import api from '@/api/index.js'
export default {
  data() {
    return {
      userName: "",
      password: "",
      flak: "hidden",
      msg:''
    };
  },
  methods: {
    login () {
      api.login({
        params:{
          userName:this.userName,
          password:this.password
        }
      }).then((res)=>{
        // console.log(res)
        if(res.data.status == 'success'){
          // this.flak = 'hiden'
          this.$router.push({name:'index'});

        }else{
          this.flak = 'visible';
          this.msg = res.data.msg;
        }
      })
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/css/login.scss";
</style>



