<template>
  <div class="header-search">
    <el-col :span="6">
      <div class="header-logo">
       <router-link :to="{name:'index'}">
         <img class="logo" src="//s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png" alt="美团">
       </router-link>
          
      </div>
    </el-col>
    <el-col :span="18">
      <div class="search-content">
        <div class="search-block">
          <input
            class="search-input"
            type="text"
            name
            id="input"
            placeholder="搜索商家或地点"
            v-model="val"
            @focus="focus"
            @blur="blur"
          >
          <button class="submit">
            <i class="el-icon-search"></i>
          </button>
          <div class="search-suggest keywords" v-if="val && showSuggest">
            <ul>
              <li v-for="item in keyList" :key="item"><a href="#">{{item}}</a></li>
            </ul>
          </div>
          <div class="search-suggest" v-show="showSuggest && !val">
          <div class="search-title">热门搜索</div>
          <div class="search-list">
            <a href="#">广州长隆欢乐世界</a>
            <a href="#">广州长隆野生动物世界</a>
            <a href="#">广州长隆水上乐园</a>
            <a href="#">广州动物园</a>
          </div>
        </div>
        </div>
       
        <div class="search-hot">
          <a href="#">广州长隆欢乐世界</a>
          <a href="#">广州长隆野生动物世界</a>
          <a href="#">广州长隆水上乐园</a>
          <a href="#">广州动物园</a>
          <a href="#">时尚酒吧</a>
        </div>
      </div>
    </el-col>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showSuggest: false,
      val:'',
      keyList: [
        "火锅",
        "火炉火",
        "火齐潮汕砂锅粥",
        "火锅自助餐",
      ]
    };
  },
  methods: {
    focus() {
      this.showSuggest = true;
    },
    blur() {
      this.showSuggest = false;
    }
  }
};
</script>
<style>
</style>


