<template>
  <div class="choose-wrap">
    <div class="choose">
      <span class="procity">{{procity}}</span>
      <i class="el-icon-caret-bottom"></i>
      <div class="choose-con">
        <dl>
          <dt>{{procity}}</dt>
          <dd v-for="item in list" :key="item.id">{{item.name}}</dd>
        </dl>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props:['list','procity']
};
</script>

<style lang="scss">
@import "@/assets/css/changeCity/choose.scss";
</style>



