<template>
  <div class="m-menu" @mouseleave="leaveNav">
    <dl class="menu-nav">
      <dt class="title">全部分类</dt>
      <dd v-for="item in menuList" :key="item.title"
      @mouseenter="enterNav(item)"
      >
        <i :class="item.icon"></i>
        <a href="#">{{item.title}}</a>
        <span class="arrow"></span>
      </dd>
    </dl>
    <div class="detail" v-if="curDetail">
      <template v-for="(item,index) in curDetail.children">
        <div class="detail-wrap" :key="item.title+index">
          <h4>
            <a href="#">{{item.title}}</a>
          </h4>
          <a href="#" class="link-more">
            更多
            <span class="arrow"></span>
          </a>
        </div>
        <div class="detail-cont" :key="item.title+index+1">
          <a href="#" v-for="i in item.children" :key="i">{{i}}</a>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      curDetail: null,
      menuList: [
        {
          title: "美食",
          icon: "food",
          children: [
            {
              title: "美食",
              children: [
                "代金券",
                "甜点饮品",
                "火锅",
                "自助餐",
                "小吃快餐",
                "日韩料理",
                "西餐",
                "聚餐宴请",
                "烧烤烤肉",
                "东北菜",
                "中式烧烤/烤串"
              ]
            }
          ]
        },
        {
          title: "外卖",
          icon: "take-out",
          children: [
            {
              title: "外卖",
              children: ["美团外卖"]
            }
          ]
        },
        {
          title: "机票/火车票",
          icon: "plane",
          children: [
            {
              title: "机票",
              children: ["国内机票",'国际/港澳台机票']
            },
            {
              title: "火车票",
              children: ["火车票"]
            }
          ]
        }
      ]
    };
  },
  methods: {
    enterNav (item) {
      this.curDetail = item;
    },
    leaveNav () {
      this.curDetail = null;
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/css/index/menu.scss";
</style>



