<template>
  <div class="page-register">
    <div class="header">
      <div class="header-cen clearfix">
        <div class="r-logo">
          <router-link class="site-logo" :to="{name:'index'}"></router-link>
        </div>
        <div class="r-login">
          <span>已有美团账号？</span>
          <router-link class="site-login" :to="{name:'login'}">登陆</router-link>
        </div>
      </div>
    </div>
    <div class="content">
      <div class="form">
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="用户名" prop="userName">
            <el-input type="text" v-model="ruleForm.userName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="创建密码" prop="password">
            <el-input type="password" v-model="ruleForm.password" ></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="rePassword">
            <el-input type="password" v-model="ruleForm.rePassword"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">注册</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="footer">
      <p>
        <a href="#">©meituan.com</a>
        <a href="#">京ICP证070791号</a>
        <span>京公网安备11010502025545号</span>
      </p>
    </div>
  </div>
</template>

<script>
import api from '@/api/index.js'
export default {
     data() {

      var validateUserName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入用户名'));
        }else if(value.length < 4 || value.length > 16){
          callback(new Error('用户名必须为4-16位的字母数字下划线组成'));
        } else {
          callback();
        }
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入用密码'));
        } else if(value.length < 6 || value.length > 16){
          callback(new Error('密码必须为6-16位的字母数字下划线组成'));
        } else {
          if (this.ruleForm.rePassword !== '') {
            this.$refs.ruleForm.validateField('rePassword');
          }
          callback();
        }
      };
      var validateRePass = (rule, value, callback) => {
        // console.log(this.ruleForm.password,this.ruleForm.rePassword)
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };

      return {
        ruleForm: {
          userName: '',
          password: '',
          rePassword: ''
        },
        rules: {
          userName: [
            { validator: validateUserName, trigger: 'blur' }
          ],
          password: [
            { validator: validatePass, trigger: 'blur' }
          ],
          rePassword: [
            { validator: validateRePass, trigger: 'blur' }
          ]
        }
      };
    },
     methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            api.register({
              params: this.ruleForm
            }).then((res)=>{
              console.log(res)
              if(res.data.status == 'success'){
                this.$router.push({name:'login'})
              }else{
                alert(res.data.msg)
              }

            })
          } else {
            alert('error submit!!');
            return false;
          }
        });
      }
    }

};
</script>

<style lang="scss">
@import "@/assets/css/register.scss";
</style>



